<template>
  <v-app>
    <v-navigation-drawer width="244" app>
      <v-sheet
          color="grey-lighten-5"
          height="128"
          width="100%"
      ></v-sheet>

      <v-list>
        <!-- 使用 v-for 遍历路由数组，并使用 router-link 作为链接 -->
        <router-link
            v-for="(route, index) in routes"
            :key="index"
            :to="route.path"
            custom
            v-slot="{ navigate }"
        >
          <v-list-item @click="navigate">
            <v-list-item-content>
              <v-list-item-title>{{ route.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </router-link>
      </v-list>
    </v-navigation-drawer>
    <v-main>
      <!-- 使用 Vue Router 显示当前路由对应的组件 -->
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
import availableRoutes from './alailableRoutes'

export default {
  name: 'App',
  components: {},
  data() {
    return {
      routes: availableRoutes
    };
  }
};
